import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import { MatButton } from '@angular/material/button';
import { MatTableDataSource } from '@angular/material/table'
import { FuseConfirmationService } from '@fuse/services/confirmation';
import { AppAlertService } from '@shared/service/app/alert.service';
import { JedsMenuService } from '@shared/service/app/menu.service';
import { IPagoTipo } from '../models/pago-tipo';
import { IFondo } from '../../../fondos/fondos/models/fondo';
import { MediosDeCobroService } from '../service/medios-de-cobro.service';
import { debounceTime, distinctUntilChanged, firstValueFrom } from 'rxjs';
import { SaveComponent } from './save/save.component';
@Component({
    selector: 'app-medios-de-cobro',
    templateUrl: './medios-de-cobro.component.html',
    styleUrls: ['./medios-de-cobro.component.scss']
})
export class MediosDeCobroComponent implements OnInit {
    public dataSource: MatTableDataSource<IPagoTipo> = new MatTableDataSource();

    medio_de_pago: IPagoTipo[];
    fondos: IFondo[] = [];

    columns = ['descripcion', 'descuento_recargo', 'numero', 'vencimiento', 'fecha_emision', 'fecha_cobro', 'duplica_numero', 'banco', 'fondo', 'activo', 'acciones']

    form: FormGroup;

    //campo del buscador
    search_descripcion: FormControl = new FormControl('');

    //variable auxiliar para el orden de listado
    sortValue: string;

    constructor(
        private mediosDePagoService: MediosDeCobroService,
        private menu: JedsMenuService,
        private confirm: FuseConfirmationService,
        private alert: AppAlertService,
    ) { }

    ngOnInit(): void {
        this.getMediosDePago();
        this.newForm();
        this.getFondos();
        this._listenCambios();
    }

    /**
     * ESta funcion cumple la tarea de escuchar sí se produce alguna modificacion en el filtro de la grilla.
     */
    _listenCambios() {
        // funcion que escucha los cambios del buscador para filtrar
        this.search_descripcion.valueChanges
            .pipe(
                debounceTime(300),
                distinctUntilChanged(),
            )
            .subscribe(() => {
                this._filterMediosDePago()
            });

        // funcion que escucha los cambios de los selectores para filtrar
        this.form.valueChanges
            .pipe(
                debounceTime(300),
                distinctUntilChanged(),
            )
            .subscribe(() => {
                this._filterMediosDePago()
            });
    }

    /**
     * Esta funcion inicializa el formulario de filtros
     */
    newForm() {
        this.form = new FormGroup({
            filtroNumero: new FormControl(('')),
            filtroVencimiento: new FormControl(('')),
            filtroFechaEmision: new FormControl(('')),
            filtroFechaCobro: new FormControl(('')),
            filtroDuplicaNumero: new FormControl(('')),
            filtroBanco: new FormControl(('')),
            filtroActivos: new FormControl((''))
        });
    }

    /**
     * Esta funcion es para listas los fondos de origen y destino en el filtro de grilla.
     * @returns fondos de origen y destino
     */
    getFondos() {
        const fondos = this.mediosDePagoService.getFondos().subscribe(fondos => { this.fondos = fondos; })
        return fondos;
    }

    /**
     *  -Funcion en el componente.-
     * - Funcion que comunica el componente con los servicios.
     * - Espera que se consulten los medios de pago del backend para almacenarlos en una variable.
     * - Teniendo la respuesta del backend en una variable, llama a la funcion 'getDataMediosDePago' pasandole por parametro los medios de pago.
     * - Le envia el filtro y el paginado al servicio.
     * @param filter Recibe los valores que haya tomado el filtro en la grilla.
     */
    async getMediosDePago(filter?: any) {
        const medios_de_pago = await firstValueFrom(this.mediosDePagoService.getMediosDePago({ filter }));
        this.getDataMediosDePago(medios_de_pago);
    }

    /**
     *  -Funcion en el componente-
     * - Esta funcion toma los datos que se la pasan por parametro para crear una tabla con AngularMaterial.
     * @param data En data se almacenan los fondos.
    */
    async getDataMediosDePago(data) {
        new Promise(async (resolve) => {
            this.medio_de_pago = data.list;
            this.dataSource = new MatTableDataSource(this.medio_de_pago);
            resolve(true);
        });
    }

    /**
     * Funcion en el componente
     * Esta funcion toma los valores que tenga el formulario de filtro y
     * los encapsula en una constante para pasarla como parametro al obtener los medios de pago. 
     * @const payload 
     */
    _filterMediosDePago($event?: any) {
        if ($event) {this.sortValue = $event.direction}

        const payload = {
            descripcion: (this.search_descripcion.value).toLowerCase(),
            lleva_numero: this.form.get('filtroNumero').value,
            lleva_vencimiento: this.form.get('filtroVencimiento').value,
            lleva_fecha_emision: this.form.get('filtroFechaEmision').value,
            lleva_fecha_cobro: this.form.get('filtroFechaCobro').value,
            lleva_banco: this.form.get('filtroBanco').value,
            duplica_numero: this.form.get('filtroDuplicaNumero').value,
            activo: this.form.get('filtroActivos').value,
            sort: this.sortValue
        }
        this.getMediosDePago(payload);
    }

    /**
     * Funcion en el componente
     * - Esta funcion guarda un medio de pago.
     * @param documentRef 
     */
    saveMedioDePago(documentRef: MatButton) {
        this.menu
            .open(SaveComponent, documentRef._elementRef, {})
            .afterClosed()
            .subscribe({
                next: response => {
                    if (response) {
                        this._filterMediosDePago();
                    }
                },
            });
    }

    /**
     * Funcion en el componente
     * - Esta funcion actualiza un medio de pago.
     * @param documentRef 
     * @param medio_de_pago 
     */
    updateMedioDePago(documentRef: MatButton, medio_de_pago: IPagoTipo) {
        this.menu
            .open(SaveComponent, documentRef._elementRef, medio_de_pago)
            .afterClosed()
            .subscribe({
                next: response => {
                    if (response) {
                        this._filterMediosDePago()
                    }
                },
            });
    }

    /**
     * Funcion en el componente
     * - Esta funcion elimina un medio de pago.
     * @param documentRef 
     * @param medio_de_pago 
     */
    deleteMedioDePago(medio_de_pago: IPagoTipo) {
        this.confirm.open({
            title: "Eliminar medio de pago " + medio_de_pago.descripcion,
            message: "¿Desea eliminar el medio de pago: " + medio_de_pago.descripcion + "?",
            dismissible: false,
            actions: {
                confirm: {
                    show: true,
                    color: 'warn',
                    label: 'Si',
                },
                cancel: {
                    show: true,
                    label: 'No',
                },
            },
        })
            .afterClosed()
            .subscribe(res => {
                if (res === 'confirmed') {
                    this.mediosDePagoService.deleteMedioDePago(medio_de_pago.idpagotipo).subscribe({
                        next: (data) => {
                            // Manejar la respuesta exitosa
                            this.alert.success("Medio de pago eliminado con éxito.");
                            if (res) {
                                this._filterMediosDePago()
                            }
                        },
                        error: (error) => {
                            // Manejar el error
                            console.error('Error en el servicio:', error);
                            this.alert.error("Este medio de pago no puede eliminarse. Esta asociado a algo más.");
                        }
                    });
                }
            });
    }
}