import { Component, Inject, OnInit } from '@angular/core';
import { IPagoTipo } from '../../models/pago-tipo';
import { MediosDeCobroService } from '../../service/medios-de-cobro.service';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { AppAlertService } from 'app/shared/service/app/alert.service';
import { JedsMenuService } from '@shared/service/app/menu.service';
import { IFondo } from 'app/modules/fondos/fondos/models/fondo';

@Component({
    selector: 'app-save-medios-de-cobro',
    templateUrl: './save.component.html',
    styleUrls: ['./save.component.scss'],
})
export class SaveComponent implements OnInit {
    columns = ['descripcion', 'porcentaje', 'fondo']
    form: FormGroup;
    medio_de_pago: IPagoTipo;
    title: string;

    fondos: IFondo[] = [];

    constructor(
        @Inject('MENU_DATA') public data: any,
        private mediosDeCobroService: MediosDeCobroService,
        public alert: AppAlertService,
        public menu: JedsMenuService,
        public fb: FormBuilder,
    ) {
        this.medio_de_pago = this.data ? this.data : null;
        this.title = this.medio_de_pago?.idpagotipo ? 'Editar Movimiento' : 'Nuevo Movimiento';
    }

    ngOnInit(): void {
        this.newForm();
        this.getFondos();
    }

    newForm() {
        this.form = this.fb.group({
            descripcion: new FormControl ((this.medio_de_pago ? this.medio_de_pago.descripcion : ''), [Validators.required]),
            porcentaje: new FormControl ((this.medio_de_pago ? this.medio_de_pago.porcentaje : ''), [Validators.required]),
            idfondo: new FormControl ((this.medio_de_pago ? this.medio_de_pago.idfondo : ''), [Validators.required]),
            activo: new FormControl ((this.medio_de_pago ? this.medio_de_pago.activo : '')),
            duplica_numero: new FormControl ((this.medio_de_pago ? this.medio_de_pago.duplica_numero : '')),
            lleva_banco: new FormControl ((this.medio_de_pago ? this.medio_de_pago.lleva_banco : '')),
            lleva_numero: new FormControl ((this.medio_de_pago ? this.medio_de_pago.lleva_numero: '')),
            lleva_vencimiento: new FormControl ((this.medio_de_pago ? this.medio_de_pago.lleva_vencimiento: '')),
            lleva_fecha_emision: new FormControl ((this.medio_de_pago ? this.medio_de_pago.lleva_fecha_emision: '')),
            lleva_fecha_cobro: new FormControl ((this.medio_de_pago ? this.medio_de_pago.lleva_fecha_cobro : '')),
        });
    }

    /**
     * Esta funcion la utilizamos cuando necesitamos listar los fondos en el saveComponent
     */
    getFondos() {
        const fondos = this.mediosDeCobroService.getFondos().subscribe(fondos => { this.fondos = fondos; })
        return fondos;
    }


    onSubmit() {
        if (!this.form.valid) {
            return this.alert.warning('Revise Los Datos Ingresados');
        } else {
            this.medio_de_pago = {
                ...this.medio_de_pago,
                lleva_numero: this.form.get('lleva_numero').value ? 1 : 0, //Se cambian los valores booleanos del formControlCheck por 1 o 0
                lleva_vencimiento: this.form.get('lleva_vencimiento').value ? 1 : 0,
                lleva_fecha_emision: this.form.get('lleva_fecha_emision').value ? 1 : 0,
                lleva_fecha_cobro: this.form.get('lleva_fecha_cobro').value ? 1 : 0,
                duplica_numero: this.form.get('duplica_numero').value ? 1 : 0,
                lleva_banco: this.form.get('lleva_banco').value ? 1 : 0,
                activo: this.form.get('activo').value ? 1 : 0,
                descripcion: this.form.get('descripcion').value,
                porcentaje: this.form.get('porcentaje').value,
                idfondo: this.form.get('idfondo').value                
            }
            if (this.medio_de_pago.idpagotipo) {
                this.mediosDeCobroService.updateMedioDePago(this.medio_de_pago)
                    .subscribe({
                        next: (data) => {
                            if (data) {
                                this.menu.close(data);
                            }
                        }
                    });
            } else {
                this.mediosDeCobroService.saveMedioDePago(this.medio_de_pago)
                    .subscribe({
                        next: (data) => {
                            if (data) {
                                this.menu.close(data);
                            }
                        }
                    });
            }
        }
    }

    onCloseMenu() {
        this.menu.close(false)
    }
}